.header {
  background-color: #2c3e50;
  color: white;
  padding: 1rem 0;
  position: sticky;
  top: 0;
  z-index: 1000;
  transition: transform 0.3s ease; /* smooth transition */
}

/* New class to hide the header */
.hidden {
  transform: translateY(-100%);
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Logo */
.logo {
  font-size: 1.5rem;
  font-weight: bold;
}

/* Nav Links */
.navLinks {
  display: flex;
  gap: 1rem;
}

/* Regular link style */
.navLink {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}

.navLink:hover {
  color: #3498db;
}

/* Mobile adjustments */
@media (max-width: 768px) {
  .nav {
    flex-direction: column;
    align-items: flex-start; /* align header items to the left */
  }
  .logo {
    margin-bottom: 0.5rem;
    align-self: flex-start;
  }
  .navLinks {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start; /* left align the nav links */
    width: 100%;
  }
}

